<template>
    <div class="recommend">
        <h2 class="title">周末去哪</h2>
        <div class="content-list">
            <div class="content" v-for="(item,index) in list " :key="index">
                <div class="left">
                    <img :src="item.imgUrl" alt="">
                </div>
                <div class="right">
                    <p class="right-title">{{item.title}}</p>
                    <p class="desc">{{item.desc}}</p>
                    <a href=""  class="detail">查看详情</a>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:"HomeWeekend",
    props:{
        list:Array
    }
}
</script>
<style  scoped>
    .title{
        height:40px;
        background-color:#eee;
        line-height :40px;
    }
    .content{
        display:flex;
        padding:5px;
    }
    .left img{
        width:100px;
        height:100px;
    }
    .right{
        display:flex;
        flex-direction: column;
        justify-content:space-between ;
        padding-left:15px;
    }
    .right-title{
        font-size:16px;
    }
    .desc{
        color:#ccc;
    }
    .detail{
        color:#fff;
        background:orange;
        width:100px;
        text-align:center ;
        padding:10px 0;
        border-radius: 5px;
    }
</style>


