<template>
    <div class="city-list" ref="wrapper">
       <div>
            <div class="current">
            <h2 class="title">当前城市</h2>
            <div class="button-list">
                <div class="button-wrapper">
                   <div class="button">北京</div>
                </div>
                <div class="button-wrapper">
                   <div class="button">上海</div>
                </div>
                <div class="button-wrapper">
                    <div class="button">广州</div>
                </div>
                <div class="button-wrapper">
                   <div class="button">上海</div>
                </div>
                <div class="button-wrapper">
                   <div class="button">深圳</div>
                </div>
            </div>
        </div>
        <div class="hot">
            <h2 class="title">热门城市</h2>
            <div class="button-list">
                <div class="button-wrapper" v-for="(item,index) in hot" :key="index">
                    <div class="button">{{item.name}}</div>
                </div>
            </div>
        </div>
        <div class="list" v-for="(item,key) in cities" :key="key" :ref="key">
            <h2 class="title">{{key}}</h2>
            <ul>
                <li class="item" v-for="(city,index) in item" :key="index">{{city.name}}</li>
            </ul>
        </div>
       </div>
    </div>
</template>
<script>
import BScroll from "better-scroll"
export default {
    name:"CityList",
    props:{
        cities:Object,
        hot:Array,
        letter:String
    },
    mounted(){
        let wrapper=this.$refs.wrapper;
        this.scroll=new BScroll(wrapper)
    },
    watch:{
        letter(){
            this.scroll.scrollToElement(this.$refs[this.letter][0]);
        }
    }
}
</script>
<style  scoped>
    .title{
        line-height: 30px;
        background-color: #eee;
    }
    .button-list{
        display:flex;
        flex-wrap: wrap;
    }
    .button-wrapper{
        width: 33.33%;
    }
    .button{
        border: 1px solid #ccc;
        border-radius: 5px;
        box-sizing: border-box;
        text-align: center;
        padding: 5px 0;
        margin: 5px 15px;
    }
    .item{
        line-height: 35px;
        padding-left: 10px;
    }
    .city-list{
        position:absolute;
        left:0;
        right: 0;
        top: 70px;
        bottom: 0;
        z-index: -999;
    }
</style>


