<template>
    <div class="icon-list">
        <mt-swipe :auto="0">
            <mt-swipe-item v-for="(page,index) of pages" :key="index">
                <div class="icon" v-for="item of page" :key="item.id">
                    <div class="icon-img">
                        <img :src="item.imgUrl" alt="">
                    </div>
                    <div class="icon-desc">{{item.desc}}</div>
                </div>
            </mt-swipe-item>
        </mt-swipe>
    </div>
</template>

<script>
export default {
    name:"HomeIconList",
    props:{
        list:Array
    },
    computed:{
        pages(){
            let pages=[];
            this.list.forEach((value,index)=>{
                var page=Math.floor(index/8)
                if(!pages[page]){
                    pages[page]=[];
                }
                pages[page].push(value);
            })
            console.log(pages)
            return pages;
        }
    }
}
</script>
<style scoped>
    .icon-list>>> .mint-swipe-indicator{
        opacity:0.6;
        background:#000
    }
    .icon-list>>>.mint-swipe-indicators.is-active{
        background:#ccc
    }
    .icon-list{
        height:250px;
        text-align: center;
    }
    .icon{
        width:25%;
        box-sizing:border-box;
        float:left;
        padding:10px;
    }
    .icon-img img{
        width:100%;
    }
</style>


