<template>
    <div class="alphabet">
        <ul class="list">
            <li class="item" 
            v-for="(item,index) in letters" 
            :key="index" @click="handleLetter"
            @touchstart.prevent="handleTouchStart"
            @touchmove="handleTouchMove"
            @touchend="handleTouchEnd"
            >
                {{item}}
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name:"CityAlphabet",
    data(){
        return{
            moveStatus:false
        }    
    },
    props:{
        cities:Object
    },
    computed:{
        letters(){
            let arr=[];
           for(let key in this.cities){
               arr.push(key)
           }
            return arr
        }
    },
    methods:{
        handleLetter(e){
            var letter=e.target.innerText;
            this.$emit("letterHandle",letter)
        },
        handleTouchStart(){
            this.moveStatus=true
        },
        handleTouchMove(e){
            if(this.moveStatus){
                var touchY=e.touches[0].clientY-100;
                var Atop=this.$refs['A'][0].offsetTop;
                var index=Math.floor((touchY-Atop)/20)
                if(index>=0 && index < this.letters.length){
                    var letter=this.letters[index]
                    this.$emit("letterHandle",letter)
                }
            }
        },
        handleTouchEnd(){
            this.moveStatus=false;
        }
    }
}
</script>
<style  scoped>
    .alphabet{
        display:flex;
        flex-direction: column;
        justify-content: center;
    }
    .list{
        position:absolute;
        right: 10px;
        top:300px;
    }
    .list .item{
        line-height: 30px;
    }
</style>


